html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** Media Queries **/
.main h1 {
  font-size: 4rem;
  margin-bottom: 0;
  font-weight: 900;
  color: #4ba5a8;
  margin-top: 0rem;
}
@media (min-width: 768px) {
  .main h1 {
    font-size: 6rem;
    margin-top: 0.5rem;
  }
}
@media (min-width: 1800px) {
  .main h1 {
    margin-top: 10rem;
  }
}
@media (min-height: 800px) {
  .main h1 {
    margin-top: 10rem;
  }
}
.main h1 span {
  color: #33777a;
}
.main h2 {
  margin: 0;
  font-weight: 700;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .main h2 {
    font-size: 4rem;
  }
}

@media (min-width: 768px) {
  .contenedor {
    padding: 0 10rem;
  }
}
.contenedor h2 {
  margin-bottom: 1rem;
  font-size: 300;
  color: #717171;
  font-size: 2.5rem;
  margin: 0;
}

.formulario {
  background-color: #f1f1f1;
  margin: auto;
  width: 90%;
  max-width: 40rem;
  padding: 5rem 4rem;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem -0.5rem rgba(0, 0, 0, 0.3);
  text-align: center;
}
@media (min-width: 768px) {
  .formulario {
    max-width: 60rem;
  }
}
.formulario .contenedor_formulario {
  margin-top: 3rem;
  display: grid;
  gap: 2.5rem;
}

.contenido_formulario {
  position: relative;
}
.contenido_formulario .form_input {
  width: 100%;
  background: none;
  font-size: 2rem;
  padding: 1rem 0.5rem;
  border: none;
  outline: none;
  border-bottom: 1px solid #717171;
  font-family: "Lato", sans-serif;
}
.contenido_formulario .form_input:focus + .form_label, .contenido_formulario .form_input:not(:placeholder-shown) + .form_label {
  transform: translateY(-1.2rem) scale(0.7);
  transform-origin: left top;
  color: #4ba5a8;
}
.contenido_formulario .form_label {
  color: #717171;
  position: absolute;
  top: 0;
  left: 0.5rem;
  transform: translateY(1rem);
  transition: transform 0.5s, color 0.3s;
}

.btn {
  background-color: #4ba5a8;
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
}
.btn:hover {
  background-color: #33777a;
}

.form_line {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.5px;
  background-color: #4ba5a8;
  transform: scale(0);
  transform-origin: left bottom;
  transition: transform 0.4s;
}

.form_input:focus ~ .form_line,
.form_input:not(:placeholder-shown) ~ .form_line {
  transform: scale(1);
}

.opciones {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
  flex-direction: column;
}
@media (min-width: 450px) {
  .opciones {
    flex-direction: row;
  }
}
.opciones a {
  color: #717171;
}
.opciones a:hover {
  color: #4ba5a8;
}

footer {
  text-align: center;
}
footer p {
  margin: 0;
  font-size: 1.5rem;
}

.registro {
  padding: 0.8rem 3rem 0 3rem;
}

.registro-btn {
  justify-content: center;
}

.header {
  background-color: #4ba5a8;
}
.header h1 {
  margin: 0;
  font-size: 5rem;
  font-weight: 900;
  color: #f4f4f4;
}
.header h1 span {
  color: #33777a;
}
.header .contenido-header {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.header .contenido-header .navegacion {
  display: flex;
  gap: 5rem;
}
.header .contenido-header .navegacion a {
  color: #f4f4f4;
  font-size: 2rem;
}
.header .contenido-header .navegacion a:hover {
  color: #33777a;
}

.contenido-main h2 {
  text-align: left;
  margin: 0;
  font-size: 3rem;
  font-weight: 700;
  color: #33777a;
}
.contenido-main h2 span {
  color: #4ba5a8;
}
.contenido-main .mensaje {
  text-align: center;
  color: #717171;
}

.btn-funcion {
  padding-top: 10rem;
  display: flex;
  justify-content: space-around;
  margin-bottom: 5rem;
}
.btn-funcion a {
  background-color: #4ba5a8;
  color: #f4f4f4;
  font-size: 2rem;
  padding: 0 1rem;
  border-radius: 1rem;
}
.btn-funcion a:hover {
  background-color: #33777a;
}

.contenido_formulario_registro {
  position: relative;
  display: flex;
  gap: 5rem;
}

.main h2 {
  text-align: center;
  color: #717171;
  font-size: 4rem;
}

.contenedor_tabla {
  width: 100%;
  margin: 8rem auto;
}

.main-lita {
  text-align: center;
  margin-top: 5rem;
  width: 100%;
  border-collapse: collapse;
}
.main-lita .theader {
  background-color: #33777a;
  border-bottom: solid 2px #245456;
}
.main-lita .theader th {
  padding: 1rem;
}
.main-lita td {
  background-color: #717171;
  color: #f4f4f4;
  font-size: 2rem;
}

.boton_actu {
  background-color: #2b8c2b;
  border-radius: 0.5rem;
  border: none;
  padding: 0.5rem;
  color: #f4f4f4;
  cursor: pointer;
}

.boton_eli {
  background-color: #a33737;
  border-radius: 0.5rem;
  border: none;
  padding: 0.5rem;
  color: #f4f4f4;
  font-size: 2rem;
  cursor: pointer;
}

.boton_reporte {
  background-color: #33777a;
  border-radius: 0.5rem;
  border: none;
  padding: 1rem;
  color: #f4f4f4;
  font-size: 2rem;
  cursor: pointer;
}

.reporte {
  text-align: center;
  margin: 0 auto;
}

.alerta {
  padding: 0.8333333333rem;
  color: #f4f4f4;
  width: 100%;
  display: block;
  margin-bottom: 0.5rem;
  text-align: center;
}
.alerta.error {
  background-color: #a33737;
}
.alerta.exito {
  background-color: #2b8c2b;
}